import { createReducer, on } from '@ngrx/store';
import { INamedListDesignerModel } from './named-list-designer.types';
import * as NamedListDesignerActions from './named-list-designer.actions';
import { INamedListDefinitionModel } from '../../named-list-designer.types';

const initialModel: INamedListDefinitionModel = {
  name: null,
  customizationOptions: {
    allowOverlay: false,
    overlayGroupId: null,
    overlayDescriptor: null,
    scope: 'BUNDLE',
    isDisabled: false,
    definitionTypeDisplayName: null
  }
};

export const initialState: INamedListDesignerModel = {
  bundleId: null,
  definitionName: null,
  isDesignMode: true,
  definitionModel: initialModel,
  definitionModelFromDefinition: initialModel,
  isDirty: false,
  savedDefinitionName: null,
  originalDefinition: null
};

const reducer = createReducer(
  initialState,

  on(NamedListDesignerActions.init, (state: INamedListDesignerModel, { payload }) => ({
    ...initialState,
    bundleId: payload.bundleId,
    definitionName: payload.definitionName
  })),

  on(
    NamedListDesignerActions.initDefinitionData,
    (state: INamedListDesignerModel, { definition, definitionModel }) => ({
      ...state,
      definitionModel,
      definitionModelFromDefinition: definitionModel,
      originalDefinition: definition
    })
  ),

  on(NamedListDesignerActions.clearFields, (state: INamedListDesignerModel) => ({
    ...state,
    definitionModel: {
      ...state.definitionModel,
      fields: [],
      queryCriteria: null,
      labelFieldId: null,
      valueFieldId: null
    }
  })),

  on(NamedListDesignerActions.markDesignerPristine, (state: INamedListDesignerModel) => ({
    ...state,
    isDirty: false
  })),

  on(NamedListDesignerActions.markDesignerDirty, (state: INamedListDesignerModel) => ({
    ...state,
    isDirty: true
  })),

  on(NamedListDesignerActions.toggleDesignMode, (state: INamedListDesignerModel) => ({
    ...state,
    isDesignMode: !state.isDesignMode
  })),

  on(
    NamedListDesignerActions.updateDefinitionModelFromDesigner,
    (state: INamedListDesignerModel, { definitionModelFromDesigner }) => ({
      ...state,
      definitionModel: { ...state.definitionModel, ...definitionModelFromDesigner }
    })
  ),

  on(NamedListDesignerActions.saveDefinitionSuccess, (state: INamedListDesignerModel, { savedDefinitionName }) => ({
    ...state,
    savedDefinitionName
  })),

  on(NamedListDesignerActions.destroy, (state: INamedListDesignerModel) => ({
    ...initialState
  }))
);

export function namedListDesignerModelReducer(state, action) {
  return reducer(state, action);
}
